import React, { Fragment } from 'react';
import ReactDatatable from '@ashvin27/react-datatable';
import { Link } from 'react-router-dom';
import { url } from 'src/helpers/helpers';
import { toast, ToastContainer } from 'react-toastify'
import { userContext } from '../../context/UserContext'
import CustomModal from '../../components/CustomModal';
import { formatDate } from 'src/helpers/helpers';
import PayModal from '../../components/PayModal';
import converter from 'number-to-words'

export default function SalesReport() {
    const { user, setLoad } = React.useContext(userContext);
    const [salesList, setSalesList] = React.useState([]);
    const [modal, setModal] = React.useState(false);
    const [payAmount, setPayAmount] = React.useState(0);
    const [paymentMode, setPaymentMode] = React.useState({
        label: "cash"
    });
    const [paymodal, setPayModal] = React.useState(false);
    const [from_Date, setFromDate] = React.useState('');
    const [to_Date, setToDate] = React.useState('');
    const [id, setId] = React.useState('');
    const [total, setTotal] = React.useState(0);
    const [saleHeader, setSaleHeader] = React.useState("");
    const [saleItem, setSaleItem] = React.useState([]);
    const [customer, setCustomer] = React.useState("");
    const [company,setCompany] = React.useState("");

    const payBill = () => {
        setLoad(true)
        async function payBillServer() {
            const formData = new FormData();
            formData.append('pay_amount', payAmount);
            formData.append('payment_mode', paymentMode.label);
            const response = await fetch(url + 'pay/sales/' + id, {
                method: 'POST',
                headers: {
                    'Authorization': user.token
                },
                body: formData
            })

            if (response) {
                const data = await response.json();

                if (data.status === 200) {
                    setPayModal(false);
                    setId('');
                    async function fetchData() {
                        const response = await fetch(url + 'fetchSalesList', {
                            method: 'GET',
                            headers: {
                                'Authorization': user?.token
                            }
                        })
                        if (response.ok === true) {
                            const data = await response.json();
                            setLoad(false)
                            if (data.status === 200) {
                                setSalesList(data.list.map((item, index) => {
                                    return {
                                        '#': index + 1,
                                        'id': item.id,
                                        'invoice_number': item.invoice_number,
                                        'customer_name': item.customer_name,
                                        'warehouse_name': item.warehouse_name,
                                        'sale_date': formatDate(item.sale_date.split(' ')[0]),
                                        'total_after_roundoff': item.total_after_roundoff,
                                        'remain_amount': item.remain_amount
                                    }
                                }));
                                setTotal(parseInt(data.total_records));
                            } else {
                                toast.error(data.message);
                            }
                        }
                    }
                    fetchData();
                } else {
                    toast.error(data.message);
                }
            }
        }
        payBillServer();
    }

    const payModal = (item, id) => {
        setPayAmount(parseFloat(item.remain_amount))
        setPayModal(true);
        setId(id);
    }

    const deleteEntry = () => {
        setLoad(true)
        async function deleteData() {
            const response = await fetch(url + 'deleteSale/' + id, {
                method: 'GET',
                headers: {
                    'Authorization': user?.token
                }
            })

            const data = await response.json();

            if (data.status === 200) {
                setModal(false);
                setId('');
                async function fetchData() {
                    const response = await fetch(url + 'fetchSalesList', {
                        method: 'GET',
                        headers: {
                            'Authorization': user?.token
                        }
                    })

                    if (response.ok === true) {
                        const data = await response.json();
                        setLoad(false)
                        if (data.status === 200) {
                            setSalesList(data.list.map((item, index) => {
                                return {
                                    '#': index + 1,
                                    'id': item.id,
                                    'customer_name': item.customer_name,
                                    'warehouse_name': item.warehouse_name,
                                    'sale_date': formatDate(item.sale_date.split(' ')[0]),
                                    'discount_amount': parseFloat(item.bill_discount_percentage) ? `${item.bill_discount_percentage}%` : item.bill_discount_amount,
                                    'total_after_roundoff': item.total_after_roundoff,
                                    'amount_paid': item.amount_paid,
                                    'remain_amount': item.remain_amount
                                }
                            }));
                            setTotal(parseInt(data.total_records));
                        } else {
                            toast.error(data.message);
                        }
                    }
                }
                fetchData();
            } else {
                toast.error(data.message)
            }
        }
        deleteData();
    }
    React.useEffect(() => {
        setLoad(true)
        async function fetchData() {
            const response = await fetch(url + 'fetchSalesList', {
                method: 'GET',
                headers: {
                    'Authorization': user?.token
                }
            })

            if (response.ok === true) {
                const data = await response.json();
                setLoad(false)
                if (data.status === 200) {
                    setSalesList(data.list.map((item, index) => {
                        return {
                            '#': index + 1,
                            'id': item.id,
                            'invoice_number': item.invoice_number,
                            'customer_name': item.customer_name,
                            'warehouse_name': item.warehouse_name,
                            'sale_date': formatDate(item.sale_date.split(' ')[0]),
                            'total_after_roundoff': item.total_after_roundoff,
                            'remain_amount': item.remain_amount
                        }
                    }));
                    setTotal(parseInt(data.total_records));
                } else {
                    toast.error(data.message);
                }
            }
        }

        fetchData();
    }, [])

    function printBillBig(id, condition) {
        async function fetchDetailSales() {
            setLoad(true);
            const response = await fetch(url + 'getSaleById/' + id, {
                headers: {
                    'Authorization': user.token
                }
            });

            if (response.ok === true) {
                const data = await response.json();
                setLoad(false)
                if (data.status === 200) {
                    console.log(data)
                    setSaleHeader(data.sale_header)
                    setSaleItem(data.sale_items)
                    setCustomer(data.customer_header);
                    setCompany(data.company_data)

                    console.log(data)

                    if (condition == "A4") {
                        let html = `
                        <style>
                        .text-secondary-d1 {
                            color: #728299!important;
                        }
                        
                        .page-content{
                            border: 1px solid black;
                        }
                        
                        .page-header {
                            margin: 0 0 1rem;
                            padding-bottom: 1rem;
                            padding-top: .5rem;
                            border-bottom: 1px dotted #e2e2e2;
                            display: -ms-flexbox;
                            display: flex;
                            -ms-flex-pack: justify;
                            justify-content: space-between;
                            -ms-flex-align: center;
                            align-items: center;
                        }
                        .page-title {
                            padding: 0;
                            margin: 0;
                            font-size: 1.75rem;
                            font-weight: 300;
                        }
                        .brc-default-l1 {
                            border-color: #dce9f0!important;
                        }
                        
                        .ml-n1, .mx-n1 {
                            margin-left: -.25rem!important;
                        }
                        .mr-n1, .mx-n1 {
                            margin-right: -.25rem!important;
                        }
                        .mb-4, .my-4 {
                            margin-bottom: 1.5rem!important;
                        }
                        
                        hr {
                            margin-top: 1rem;
                            margin-bottom: 1rem;
                            border: 0;
                            border-top: 1px solid rgba(0,0,0,.1);
                        }
                        
                        .text-grey-m2 {
                            color: #888a8d!important;
                        }
                        
                        .text-success-m2 {
                            color: #86bd68!important;
                        }
                        
                        .font-bolder, .text-600 {
                            font-weight: 600!important;
                        }
                        
                        .text-110 {
                            font-size: 110%!important;
                        }
                        .text-blue {
                            color: #478fcc!important;
                        }
                        .pb-25, .py-25 {
                            padding-bottom: .75rem!important;
                        }
                        
                        .pt-25, .py-25 {
                            padding-top: .75rem!important;
                        }
                        .bgc-default-tp1 {
                            background-color: rgba(121,169,197,.92)!important;
                        }
                        .bgc-default-l4, .bgc-h-default-l4:hover {
                            background-color: #f3f8fa!important;
                        }
                        .page-header .page-tools {
                            -ms-flex-item-align: end;
                            align-self: flex-end;
                        }
                        
                        .btn-light {
                            color: #757984;
                            background-color: #f5f6f9;
                            border-color: #dddfe4;
                        }
                        .w-2 {
                            width: 1rem;
                        }
                        
                        .text-120 {
                            font-size: 120%!important;
                        }
                        .text-primary-m1 {
                            color: #4087d4!important;
                        }
                        
                        .text-danger-m1 {
                            color: #dd4949!important;
                        }
                        .text-blue-m2 {
                            color: #68a3d5!important;
                        }
                        .text-150 {
                            font-size: 150%!important;
                        }
                        .text-60 {
                            font-size: 60%!important;
                        }
                        .text-grey-m1 {
                            color: #7b7d81!important;
                        }
                        .align-bottom {
                            vertical-align: bottom!important;
                        }
                        
                        
                        .table-div{
                            border: 1px solid black;
                        }
                        </style>
                    `
                        html = html + document.getElementById("a4-print").innerHTML;

                        var myWindow = window.open('', 'Receipt', 'height=400,width=600');
                        myWindow.document.write(`<html><head><title>Reciepts</title><link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap@4.6.1/dist/css/bootstrap.min.css"
                    integrity="sha384-zCbKRCUGaJDkqS1kPbPd7TveP5iyJE0EjAuZQTgFLD2ylzuqKfdKlfG/eSrtxUkn" crossorigin="anonymous">`);
                        myWindow.document.write("</head><body>");
                        myWindow.document.write(html);
                        myWindow.document.write('</body></html>');
                        myWindow.document.close();

                        myWindow.onload = function () {
                            myWindow.focus();
                            myWindow.print();
                        }
                    } else {
                        // if thermal printer is here
                        let html = `
                        <style>
                        * {
                        margin: 0;
                        padding: 0;
                        box-sizing: border-box;
                    }
                    
                    .bill-main {
                        width: 360px;
                        padding: 15px 15px;
                    }
                    
                    small{
                        display: inline-block;
                        padding-bottom: 4px;
                    }
                    
                    .text-uppercase {
                        text-transform: uppercase
                    }
                    
                    .text-right{
                        text-align: right;
                    }
                    
                    .FB {
                        font-weight: bold;
                    }
                    
                    .DFlex {
                        display: flex;
                    }
                    
                    .JC-between{
                        justify-content: space-between;
                    }
                    
                    .mb-1{
                        margin-bottom: 4px !important;
                    }
                    
                    .mb-3{
                        margin-bottom: 1rem;
                    }
                    
                    .bill-main p {
                        color: darkslategrey;
                        font-family: sans-serif;
                        font-size: 14px;
                        margin-bottom: 8px;
                    }
                    
                    .bill-title.address {
                        width: 260px;
                        margin: 0 auto;
                    }
                    
                    .bill-main .bill-title {
                        text-align: center;
                        margin-bottom: 20px;
                    }
                    
                    .AlignItemsEnd {
                        align-items: flex-end;
                    }
                    
                    .upper-section-first-div {
                        flex-basis: 60%;
                    }
                    
                    .bill-upper-section {
                        margin: 12px 0;
                    }
                    
                    .bill-mid-section {
                        min-height: 105px;
                        border-bottom: 1.6px dashed darkslategrey;
                        margin-top: 1rem;
                    }
                    
                    .bill-mid-section table {
                        width: 100%;
                        border-collapse: collapse;
                    }
                    
                    .textRight {
                        text-align: right;
                    }
                    
                    .table-head {
                        border: 1.6px solid darkslategrey;
                        padding: 0 8px;
                    }
                    
                    .table-head td p {
                        margin: 4px 0;
                    }
                    
                    .bill-mid-section tr td:first-child {
                        padding-left: 8px;
                    }
                    
                    .bill-mid-section tbody tr:first-child td p{
                        padding-top: .5rem;
                    }
                    
                    .bill-mid-section tr td:nth-child(2) {
                        width: 170px;
                        padding-left: 8px;
                    }
                    
                    .bill-mid-section tr td:last-child {
                        padding-right: 8px;
                    }
                    
                    .bill-lower-section {
                        margin-top: 8px;
                    }
                    
                    .bill-lower-section .right-section {
                        flex-basis: 80%;
                    }
                    
                    .thanks {
                        text-align: center;
                        margin: 10px 0;
                    }
                        </style>
                        `
                        html = html + document.getElementById("thermal-print").innerHTML;

                        var myWindow = window.open('', 'Receipt', 'height=400,width=600');
                        myWindow.document.write(`<html><head><title>Reciepts</title>`);
                        myWindow.document.write("</head><body>");
                        myWindow.document.write(html);
                        myWindow.document.write('</body></html>');
                        myWindow.document.close();

                        myWindow.onload = function () {
                            myWindow.focus();
                            myWindow.print();
                        }
                    }
                } else {
                    toast.error(data.message);
                }
            }
        }
        fetchDetailSales()
    }

    const columns = [
        {
            key: "#",
            text: "#",
            className: "#",
            sortable: false,
        },
        {
            key: "invoice_number",
            text: "Invoice Number",
            className: "invoice_number",
            sortable: true
        },
        {
            key: "warehouse_name",
            text: "Warehouse Name",
            className: "warehouse_name",
            sortable: true
        },
        {
            key: "customer_name",
            text: "Customer Name",
            className: "customer_name",
            sortable: true
        },
        {
            key: "sale_date",
            text: "Sale Date",
            className: "sale_date",
            sortable: true
        },
        {
            key: "total_after_roundoff",
            text: "Bill Amount",
            className: "bill_amount",
            sortable: true
        },
        {
            key: "remain_amount",
            text: "Remain Amount",
            className: "remain_amount",
            sortable: true
        },
        {
            key: "action",
            text: "Action",
            className: "action",
            width: 100,
            sortable: false,
            cell: record => {
                return (
                    <Fragment>
                        {parseFloat(record.remain_amount) !== 0 && <i
                            style={{ cursor: 'pointer' }}
                            onClick={() => payModal(record, record.id)}
                            className="fa fa-money mr-2"
                            aria-hidden="true">
                        </i>
                        }
                        <Link
                            to={`/edit/sales/${record.id}`}
                            style={{ marginRight: '5px' }}>
                            <i className="fa fa-pencil mr-2"></i>
                        </Link>
                        <i
                            style={{ cursor: "pointer" }}
                            onClick={() => showModal(record.id)}
                            class="fa fa-trash"
                            aria-hidden="true">
                        </i>
                        <div class="dropdown">
                            <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-expanded="false">
                                <i className='ml-1 fa fa-print'></i>
                            </button>
                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                <a class="dropdown-item" href="javascript:void(0);" onClick={() => printBillBig(record.id, "A4")}>A4</a>
                                <a class="dropdown-item" href="javascript:void(0);" onClick={() => printBillBig(record.id, "Thermal")}>Thermal</a>
                            </div>
                        </div>
                    </Fragment>
                );
            }
        }
    ];
    const config = {
        page_size: 10,
        length_menu: [10, 20, 50],
        button: {
            excel: false,
            print: false,
            extra: false,
        }
    }

    const showModal = value => {
        setId(value);
        setModal(true)
    }

    async function fetchData(query) {
        setLoad(true);
        const response = await fetch(url + query, {
            method: 'GET',
            headers: {
                'Authorization': user?.token
            }
        })
        if (response.ok === true) {
            const data = await response.json();
            setLoad(false)
            if (data.status === 200) {
                setSalesList(data.list.map((item, index) => {
                    return {
                        '#': index + 1,
                        'id': item.id,
                        'invoice_number': item.invoice_number,
                        'customer_name': item.customer_name,
                        'warehouse_name': item.warehouse_name,
                        'sale_date': formatDate(item.sale_date.split(' ')[0]),
                        'total_after_roundoff': item.total_after_roundoff,
                        'remain_amount': item.remain_amount
                    }
                }));
                setTotal(parseInt(data.total_records));
            } else {
                toast.error(data.message);
            }
        }
    }

    function handleSubmit(e) {
        e.preventDefault();
        if (to_Date && from_Date) {
            setLoad(true);
            async function submit() {
                const response = await fetch(url + `fetchSalesList?to=${to_Date}&from=${from_Date}`, {
                    method: 'GET',
                    headers: {
                        'Authorization': user?.token
                    },
                });
                if (response.ok === true) {
                    const data = await response.json();
                    setLoad(false)
                    if (data.status === 200) {
                        setSalesList(data.list.map((item, index) => {
                            return {
                                '#': index + 1,
                                'id': item.id,
                                'invoice_number': item.invoice_number,
                                'customer_name': item.customer_name,
                                'warehouse_name': item.warehouse_name,
                                'sale_date': formatDate(item.sale_date.split(' ')[0]),
                                'total_after_roundoff': item.total_after_roundoff,
                                'remain_amount': item.remain_amount
                            }
                        }));
                        setTotal(parseInt(data.total_records));
                    } else {
                        toast.error(data.message)
                    }
                } else {
                    toast.error('Oops something went wrong!')
                }
            }
            submit().catch(err => toast.error('Internal server error!. Please try again later'))
        } else {
            toast.error('Please fill the to and from date')
        }
    }
    const handleChange = (e) => {
        let query = `fetchSalesList?page_number=${e.page_number}&page_size=${e.page_size}&filter_value=${e.filter_value}&to=${to_Date}&from=${from_Date}`;
        if (e.sort_order) {
            let sort = JSON.stringify(e.sort_order);
            query = `fetchSalesList?page_number=${e.page_number}&page_size=${e.page_size}&filter_value=${e.filter_value}&sort_order=${sort}&to=${to_Date}&from=${from_Date}`;
        }
        fetchData(query);
    }
    return (
        <section>
            <ToastContainer />
            <CustomModal modal={modal} setModal={setModal} deleteEntry={deleteEntry} />
            <PayModal paymodal={paymodal} setPayModal={setPayModal} payAmount={payAmount} setPayAmount={setPayAmount} paymentMode={paymentMode} payBill={payBill} setPaymentMode={setPaymentMode} />
            <form onSubmit={(e) => handleSubmit(e)}>
                <div class="form-row create-purchase-header">
                    <div class="form-group col-md-3">
                        <label for="date">From</label>
                        <input value={from_Date} onChange={e => setFromDate(e.target.value)} type="date" class="form-control" id="date" required />
                    </div>
                    <div class="form-group col-md-3">
                        <label for="date">To</label>
                        <input value={to_Date} onChange={e => setToDate(e.target.value)} type="date" class="form-control" id="date" required />
                    </div>
                    <div class="form-group col-md-3">
                        <button type="Submit" class="font-weight-bold my-4 py-2 px-4 btn btn-success">Filter</button>
                    </div>
                </div>
            </form>
            <ReactDatatable
                config={config}
                records={salesList}
                columns={columns}
                total_record={total}
                onChange={(e) => handleChange(e)}
                dynamic={true}
            />
            {/* thermal bill structure */}
            {saleItem.length > 0 && (
                <div style={{ display: 'none' }} id="thermal-print">
                    <div class="bill-main">
                        <p class="bill-title FB">{saleHeader.default_gst == "true" ? "Tax Invoice" : "Estimation"}</p>
                        <div class="DFlex JC-between">
                            <div class="width: 70%;">
                                <small>from</small>
                                <p style={{ fontSize: 22 }} class="mb-1 text-uppercase FB">{saleHeader.company_name}</p>
                                <p class="mb-1 address"><span class="FB">Address:</span> {company.address}</p>
                                {saleHeader.default_gst == "true" && <p class="mb-1"><span class="FB">GST :</span> {company.gst}</p>}
                                <p class="mb-1"><span class="FB">Mobile:</span> {company.mobile}</p>
                            </div>
                            <div>
                                <div class="mb-3" style={{ marginTop: "1.4rem" }}>
                                    <p class="text-right mb-1 FB">Bill No:</p>
                                    <p class="text-right mb-1">#{saleHeader.invoice_number}</p>
                                </div>
                                <div>
                                    <p class="text-right mb-1 FB">Date:</p>
                                    <p class="text-right mb-1">{saleHeader.sale_date}</p>
                                </div>
                            </div>
                        </div>

                        <div class="width: 70%;">
                            <small>to</small>
                            <p style={{ fontSize: 22 }} class="mb-1 text-uppercase FB">{customer.name}</p>
                            <p class="mb-1 address"><span class="FB">Address:</span> {customer.address}</p>
                            {saleHeader.default_gst == "true" && <p class="mb-1"><span class="FB">GST :</span> {customer.gst_number}</p>}
                            <p class="mb-1"><span class="FB">Mobile:</span> {customer.mobile}</p>
                        </div>

                        <hr />

                        <div class="bill-mid-section">
                            <table>
                                <thead>
                                    <tr class="table-head">
                                        <td>
                                            <p>#</p>
                                        </td>
                                        <td>
                                            <p>Product</p>
                                        </td>
                                        <td>
                                            <p class="textRight">QTY</p>
                                        </td>
                                        <td>
                                            <p class="textRight">Amount</p>
                                        </td>
                                        <td>
                                            <p class="textRight">Total</p>
                                        </td>
                                    </tr>
                                </thead>

                                <tbody>
                                    {saleItem.map((item, index) => (
                                        <tr key={index}>
                                            <td>{index + 1}</td>
                                            <td>
                                                {item.product_name}
                                            </td>
                                            <td>{item.qty}</td>
                                            <td>{item.sale_price}</td>
                                            <td>{item.total}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>

                        <div class="bill-lower-section">
                            <div class="DFlex">
                                <p class="right-section">Order Value</p>
                                <p>{(parseFloat(saleHeader.total_after_roundoff) - parseFloat(saleHeader.taxable_amount) - parseFloat(saleHeader.item_discount_amount)).toFixed(2)}</p>
                            </div>
                            <div class="DFlex">
                                <p class="right-section">total tax</p>
                                <p>{saleHeader.taxable_amount}</p>
                            </div>
                            <div class="DFlex">
                                <p class="right-section">discount(Amt.)</p>
                                <p>{saleHeader.item_discount_amount}</p>
                            </div>
                            <div class="DFlex">
                                <p class="right-section FB">Grand Total</p>
                                <p>{saleHeader.total_after_roundoff}</p>
                            </div>
                        </div>
                        <hr />
                        <h2 class="thanks">THANKS FOR YOUR BUSINESS</h2>

                    </div>
                </div>
            )}
            {/* a4 bill structure */}
            {saleItem.length > 0 && (
                <div style={{ display: 'none' }} id='a4-print' class="page-content">
                    <div class="page-header justify-content-center pt-3 text-blue-d2">
                        <h1 class="page-title text-secondary-d1">{saleHeader.default_gst == "true" ? "Tax Invoice" : "Estimation"}</h1>
                    </div>

                    <div class="container px-0">
                        <div class="row mt-4">
                            <div class="col-12 col-lg-12">
                                <div class="invoice-header d-flex justify-content-between">
                                    <div class="invoice-from pr-4">
                                        <small>from</small>
                                        <address class="m-t-5 m-b-5">
                                            <strong class="text-inverse">{saleHeader.company_name}</strong>
                                            <p class="my-1"><span class="text-600">Address:</span> {company.address}</p>
                                            <p class="my-1"><span class="text-600">Phone:</span> {company.mobile}</p>
                                            {saleHeader.default_gst == "true" && <p class="my-1"><span class="text-600">GST:</span> {company.gst}</p>}
                                        </address>
                                    </div>
                                    <div class="invoice-to px-4">
                                        <small>to</small>
                                        <address class="m-t-5 m-b-5">
                                            <strong class="text-inverse">{customer.name}</strong>
                                            <p class="my-1"><span class="text-600">Address:</span>
                                                {customer.address}
                                            </p>
                                            <p class="my-1"><span class="text-600">Phone:</span> {customer.mobile}</p>
                                            {saleHeader.default_gst == "true" && <p class="my-1"><span class="text-600">GST:</span> {customer.gst_number}</p>}

                                        </address>
                                    </div>
                                    <div class="invoice-date pl-4">

                                        <div class="my-2"><i class="fa fa-circle text-blue-m2 text-xs mr-1"></i> <span
                                            class="text-600 text-90">Invoice No:</span> #{saleHeader.invoice_number}</div>

                                        <div class="my-2"><i class="fa fa-circle text-blue-m2 text-xs mr-1"></i> <span
                                            class="text-600 text-90">Date:</span> {saleHeader.sale_date}</div>
                                    </div>
                                </div>
                                <div class="row border-b-2 brc-default-l2"></div>

                                <div class="table-responsive table-div">
                                    <table class="table table-striped brc-default-l1">
                                        <thead class="bg-none bgc-default-tp1">
                                            <tr class="">
                                                <th class="opacity-2">#</th>
                                                <th>Goods</th>
                                                <th>HSN Code</th>
                                                <th>Qty</th>
                                                <th>Unit</th>
                                                <th>Rate</th>
                                                <th>Amount</th>
                                                {saleHeader.default_gst == "true" && <th>Tax Value</th>}
                                                <th>Discount</th>
                                                <th width="140">Total</th>
                                            </tr>
                                        </thead>

                                        <tbody class="text-95 text-secondary-d3">
                                            <tr></tr>
                                            {saleItem.map((item, index) => (
                                                <tr key={index}>
                                                    <td>{index + 1}</td>
                                                    <td>
                                                        {item.product_name}
                                                    </td>
                                                    <td>{item.hsbn}</td>
                                                    <td>{item.qty}</td>
                                                    <td>{item.product_unit}</td>
                                                    <td>{item.sale_price}</td>
                                                    <td>{(parseFloat(item.qty) * parseFloat(item.sale_price)).toFixed(2)}</td>
                                                    {saleHeader.default_gst == "true" && <td>{item.tax_percent}</td>}
                                                    <td>{item.discount_amount}</td>
                                                    <td>{item.total}</td>
                                                </tr>
                                            ))}

                                        </tbody>
                                    </table>
                                </div>


                                <div class="row mt-3">
                                    <div class="col-12 col-sm-7 text-grey-d2 text-95 mt-2 mt-lg-0">
                                        Extra note such as company or payment information...
                                        <p className='text-capitalize'>Amount In Rupees : {converter.toWords(parseInt(saleHeader.total_after_roundoff))} Only</p>
                                    </div>

                                    <div class="col-12 col-sm-5 text-grey text-90 order-first order-sm-last">
                                        <div class="row my-2">
                                            <div class="col-7 text-right">
                                                SubTotal
                                            </div>
                                            <div class="col-5">
                                                <span class="text-120 text-secondary-d1">
                                                    {(parseFloat(saleHeader.total_after_roundoff) - parseFloat(saleHeader.taxable_amount) - parseFloat(saleHeader.item_discount_amount)).toFixed(2)}
                                                </span>
                                            </div>
                                        </div>

                                        <div class="row my-2">
                                            <div class="col-7 text-right">
                                                Tax
                                            </div>
                                            <div class="col-5">
                                                <span class="text-110 text-secondary-d1">
                                                    {saleHeader.taxable_amount}
                                                </span>
                                            </div>
                                        </div>

                                        <div class="row my-2">
                                            <div class="col-7 text-right">
                                                Discount
                                            </div>
                                            <div class="col-5">
                                                <span class="text-110 text-secondary-d1">
                                                    {saleHeader.item_discount_amount}
                                                </span>
                                            </div>
                                        </div>

                                        <div class="row my-2 align-items-center bgc-primary-l3 p-2">
                                            <div class="col-7 text-right">
                                                Total Amount
                                            </div>
                                            <div class="col-5">
                                                <span class="text-150 text-success-d3 opacity-2">
                                                    {saleHeader.total_after_roundoff}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <hr />

                                <div>
                                    <span class="text-secondary-d1 text-105">Thank you for your business</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* end print sales bill */}

        </section>
    )
}